import {
  CButton,
  CCol,
  CContainer,
  CForm,
  CFormInput,
  CFormLabel,
  CFormSelect,
  CFormTextarea,
  CImage,
  CRow,
} from '@coreui/react';
import axios from 'axios';
import React, { useCallback, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { toast } from 'react-toastify';
import { api, headers } from 'src/views/api';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Slider from 'react-slick';
import CIcon from '@coreui/icons-react';
import { cilTrash } from '@coreui/icons';

const AddProduct = () => {
  const [product, setProduct] = useState({
    name: '',
    description: '',
    price: '',
    categories: '',
  });
  const [categories, setCategoeies] = useState([]);
  const [updateMode, setUpdateMode] = useState(false);
  const [file, setFile] = useState('');
  const [fileData, setFileData] = useState([]);
  const [images, setImages] = useState([]);
  const [filters, setFilters] = useState([]);
  const [selectedFilterChild, setSelectedFilterChild] = useState([]);
  const [subFilters, setSubFilters] = useState([]);
  const [parent, setParent] = useState(null);
  const { push } = useHistory();

  const handleChange = (e) =>
    setProduct({ ...product, [e.target.name]: e.target.value });

  const handleUpload = useCallback(
    async (id) => {
      if (fileData?.length && id) {
        try {
          for (let i = 0; i < fileData.length; i++) {
            const bodyFormData = new FormData();
            bodyFormData.append('image', fileData[i]);

            await axios({
              method: 'post',
              url: `${api}/api/product/upload/${id}`,
              data: bodyFormData,
              headers: {
                'Content-Type': 'multipart/form-data',
                'x-auth-token': localStorage.token,
              },
            });
          }
        } catch (error) {
          console.log(error?.response);
        }
      }
    },
    [fileData]
  );

  const persianNumbers = [
      /۰/g,
      /۱/g,
      /۲/g,
      /۳/g,
      /۴/g,
      /۵/g,
      /۶/g,
      /۷/g,
      /۸/g,
      /۹/g,
    ],
    arabicNumbers = [
      /٠/g,
      /١/g,
      /٢/g,
      /٣/g,
      /٤/g,
      /٥/g,
      /٦/g,
      /٧/g,
      /٨/g,
      /٩/g,
    ],
    fixNumbers = function (str) {
      if (typeof str === 'string') {
        for (var i = 0; i < 10; i++) {
          str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
        }
      }
      return str;
    };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const filtered = selectedFilterChild.map((item) => {
      return {
        parent: item.parent,
        value: item._id,
        name: item.name,
      };
    });
    console.log(filtered);

    try {
      product.price = fixNumbers(product.price);
      product.discount = fixNumbers(product.discount);
      product.filters = filtered;

      const hRef = window.location?.href?.indexOf('=');
      const productId = window.location?.href?.slice(hRef + 1);

      if (hRef !== -1) {
        const data = {
          name: product?.name,
          description: product?.description,
          price: product?.price,
          categories: product?.categories,
          images: product?.images,
          filters: product?.filters,
        };

        const response = await axios.put(
          `${api}/api/product/${productId}`,
          data,
          {
            headers,
          }
        );

        if (response?.status === 200) {
          handleUpload(productId);
          toast.success('محصول با موفقیت ویرایش شد');
          push('/products');
        }
      } else {
        const response = await axios.post(`${api}/api/product`, product, {
          headers,
        });

        if (response?.status === 200) {
          handleUpload(response?.data?._id);
          toast.success('محصول با موفقیت ثبت شد');
          push('/products');
        }
      }
    } catch (error) {
      console.log(error);
    }
  };

  const renderCategories = (categories) => {
    let myCategories = [];

    for (let category of categories) {
      myCategories.push(
        <ul style={{ padding: 8 }}>
          <li
            style={{ listStyleType: 'none' }}
            onClick={async () => {
              setSelectedFilterChild([]);
              const categoryId = category._id;
              await axios
                .get(`${api}/api/filter/category/${categoryId}`, { headers })
                .then((res) => {
                  setFilters(res.data);
                })
                .catch((err) => {
                  console.log(err);
                });
            }}
          >
            <input
              style={{
                marginLeft: 8,
                position: 'relative',
                top: 2,
                cursor: 'pointer',
              }}
              type='checkbox'
              value={category._id}
              onChange={(e) =>
                setProduct({
                  ...product,
                  categories: e.target.value,
                })
              }
              checked={product?.categories === category._id}
            />
            {category?.name}
            {category?.children?.length > 0 && (
              <ul style={{ padding: 0, paddingRight: 10 }}>
                {renderCategories(category.children)}
              </ul>
            )}
          </li>
        </ul>
      );
    }

    return myCategories;
  };

  useEffect(async () => {
    const hRef = window.location?.href?.indexOf('=');
    const productId = window.location?.href?.slice(hRef + 1);

    if (hRef !== -1) {
      setUpdateMode(true);
      const response = await axios.get(`${api}/api/product/${productId}`);
      setProduct({
        name: response?.data?.name,
        description: response?.data?.description,
        price: response?.data?.price,
        categories: response?.data?.categories,
        images: response?.data?.images,
        filters: response?.data?.filters,
      });
      setSelectedFilterChild(
        response?.data?.filters.map((item) => {
          return {
            parent: item.parent,
            _id: item.value,
            name: item.name,
          };
        })
      );
      await axios
        .get(`${api}/api/filter/category/${response?.data?.categories}`, {
          headers,
        })
        .then((res) => {
          setFilters(res.data);
        });
      setFile(
        `${api}/uploads/admin/product/${productId}/${
          response?.data?.images?.[response?.data?.images?.length - 1]
        }`
      );
      setImages(
        response?.data?.images?.map((el) => {
          return `${api}/uploads/admin/product/${productId}/${el}`;
        })
      );
    }
    const response = await axios.get(`${api}/api/categories`);
    setCategoeies(response?.data?.categoryList);
  }, []);

  var settings = {
    dots: true,
    infinite: true,
    speed: 500,
    slidesToShow: 1,
    slidesToScroll: 1,
    rtl: true,
  };

  console.log(subFilters);

  return (
    <CContainer>
      <CRow>
        <CCol md={12}>
          <div class='d-flex flex-row docs-highlight mb-3 justify-content-center'>
            <div class='p-2 docs-highlight'>
              <CForm onSubmit={handleSubmit}>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>نام محصول</p>
                  </CFormLabel>
                  <CFormInput
                    type='text'
                    id='name'
                    placeholder='نام محصول را اینجا وارد کنید'
                    name='name'
                    onChange={handleChange}
                    value={product.name}
                  />
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>عکس محصول</p>
                  </CFormLabel>
                  <div style={{ width: 500, marginBottom: 48 }}>
                    <Slider {...settings}>
                      {images?.length &&
                        images.map((el) => (
                          <div style={{ position: 'relative' }}>
                            <div>
                              <CIcon
                                icon={cilTrash}
                                style={{
                                  color: 'red',
                                  cursor: 'pointer',
                                  position: 'relative',
                                  top: 40,
                                  left: 10,
                                  borderRadius: '50%',
                                  border: '1px solid red',
                                  padding: 3,
                                  width: 25,
                                  height: 25,
                                }}
                                title='حذف عکس'
                                onClick={() => {
                                  const filter = images.filter(
                                    (elem) => elem !== el
                                  );

                                  setImages(filter);
                                  setProduct({
                                    ...product,
                                    images: product?.images?.filter(
                                      (image) => image !== el.split('/').pop()
                                    ),
                                  });
                                }}
                              />
                            </div>
                            <img width={'100%'} src={el} alt='slider' />
                          </div>
                        ))}
                    </Slider>
                  </div>
                  <div className='form-file'>
                    <input
                      type='file'
                      className='form-file-input'
                      id='customFile'
                      style={{ display: 'none' }}
                      onChange={(e) => {
                        setFileData([...fileData, e.target.files[0]]);
                        setFile(URL.createObjectURL(e.target.files[0]));
                        setImages([
                          ...images,
                          URL.createObjectURL(e.target.files[0]),
                        ]);
                      }}
                    />
                    <label className='form-file-label' htmlFor='customFile'>
                      <span className=' m-3'>عکس محصول را انتخاب کنید</span>
                      <span className='btn btn-primary'>کلیک کنید</span>
                    </label>
                  </div>
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>قیمت</p>
                  </CFormLabel>
                  <CFormInput
                    type='text'
                    id='price'
                    placeholder='قیمت محصول'
                    name='price'
                    onChange={handleChange}
                    value={product.price}
                  />
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>توضیحات محصول</p>
                  </CFormLabel>
                  <CFormTextarea
                    id='productDescription'
                    rows='3'
                    name='description'
                    onChange={handleChange}
                    value={product.description}
                  ></CFormTextarea>
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>دسته بندی محصول</p>
                  </CFormLabel>
                  <div className='d-flex justify-content-between flex-wrap'>
                    {categories && renderCategories(categories)}
                  </div>
                </div>
                <div className='mb-3'>
                  <CFormLabel htmlFor='name'>
                    <p class='fs-3'>افزودن فیلتر</p>
                  </CFormLabel>
                  <div className='d-flex  flex-wrap'>
                    {filters?.map((el) => (
                      <div
                        className='d-flex flex-column p-4 pt-0 w-25'
                        style={{ cursor: 'pointer' }}
                      >
                        <div className='d-flex'>
                          <div>
                            <p className='fs-5 '>{el.name}</p>
                            <select
                              style={{ minWidth: 150, marginBottom: 8 }}
                              onChange={async (e) => {
                                const result = await axios.get(
                                  `${api}/api/filter/category/value/${el?._id}/${e?.target?.value}`,
                                  { headers }
                                );
                                if (result?.data?.[0]?.values?.length) {
                                  setParent(el?._id);
                                  setSubFilters(result?.data?.[0]?.values);
                                }
                              }}
                            >
                              <option>انتخاب کنید</option>
                              {el?.values?.map((value) => (
                                <option value={value?._id}>
                                  {value?.name}
                                </option>
                              ))}
                            </select>
                            {subFilters?.length && el?._id === parent ? (
                              <select
                                style={{ minWidth: 150, marginBottom: 8 }}
                                onChange={(e) => console.log(e.target.value)}
                              >
                                <option>انتخاب کنید</option>
                                {subFilters?.map((sub) => (
                                  <option value={sub?._id}>{sub.name}</option>
                                ))}
                              </select>
                            ) : null}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
                <div className='mb-3'>
                  <CButton color='success' type='submit'>
                    {updateMode ? 'ویرایش' : 'افزودن'}
                  </CButton>
                </div>
              </CForm>
            </div>
          </div>
        </CCol>
      </CRow>
    </CContainer>
  );
};

export default AddProduct;
