import {
  CButton,
  CFormInput,
  CFormTextarea,
  CInputGroup,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
} from '@coreui/react';
import axios from 'axios';
import React, { useState } from 'react';
import { api, headers } from '../api';

const CreateModal = ({ getAll }) => {
  const [visible, setVisible] = useState(false);
  const [data, setData] = useState({
    name: '',
    description: '',
    price: '',
  });

  const persianNumbers = [
      /۰/g,
      /۱/g,
      /۲/g,
      /۳/g,
      /۴/g,
      /۵/g,
      /۶/g,
      /۷/g,
      /۸/g,
      /۹/g,
    ],
    arabicNumbers = [
      /٠/g,
      /١/g,
      /٢/g,
      /٣/g,
      /٤/g,
      /٥/g,
      /٦/g,
      /٧/g,
      /٨/g,
      /٩/g,
    ],
    fixNumbers = function (str) {
      if (typeof str === 'string') {
        for (var i = 0; i < 10; i++) {
          str = str.replace(persianNumbers[i], i).replace(arabicNumbers[i], i);
        }
      }
      return str;
    };

  const handleChange = (e) => {
    setData({
      ...data,
      [e.target.name]: e.target.value,
    });
  };

  const handleCreate = async () => {
    await axios.post(
      `${api}/api/plan`,
      {
        name: data?.name,
        price: Number(fixNumbers(data?.price)),
        description: data?.description,
      },
      {
        headers: headers,
      }
    );

    getAll();
    setVisible(!visible);
  };

  return (
    <div>
      <CButton onClick={() => setVisible(!visible)}>افزودن پلن</CButton>
      <CModal visible={visible} onClose={() => setVisible(false)}>
        <CModalHeader onClose={() => setVisible(false)}>
          <CModalTitle>افزودن پلن</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CInputGroup className='mb-3'>
            <CFormInput
              placeholder='نام پلن'
              name='name'
              value={data.name}
              onChange={handleChange}
            />
          </CInputGroup>
          <CInputGroup className='mb-4'>
            <CFormInput
              type='text'
              placeholder='قیمت پلن'
              name='price'
              value={data.price}
              onChange={handleChange}
            />
          </CInputGroup>
          <CInputGroup>
            <CFormTextarea
              placeholder='توضیحات پلن'
              rows='3'
              name='description'
              onChange={handleChange}
              value={data.description}
            ></CFormTextarea>
          </CInputGroup>
        </CModalBody>
        <CModalFooter>
          <CButton color='secondary' onClick={() => setVisible(false)}>
            خروج
          </CButton>
          <CButton
            disabled={!data?.name || !data?.price || !data?.description}
            color='primary'
            onClick={handleCreate}
          >
            افزودن
          </CButton>
        </CModalFooter>
      </CModal>
    </div>
  );
};

export default CreateModal;
